.Carousel {
  width: 100%;
  height: 100%;
}
.Carousel_slides {
  overflow-y: visible;
  height: inherit;
  display: flex;
  flex-direction: column;
  background-position: center top;
  background-repeat: no-repeat;
  background-size: 100% 110vh;
  padding-left: 10vw;
}
.Carousel_slides:focus {
  outline: none;
  box-shadow: none;
}
.Carousel_slides_image {
  height: inherit;
  background-position: center top;
  background-repeat: no-repeat;
  background-size: cover;
}
.Carousel_slides_text {
  align-items: center;
  color: white;
  display: flex;
  flex-direction: column;
  font-size: 32px;
  font-family: "Questrial", Sans-serif;
  font-weight: 500;
  height: 100%;
  justify-content: center;
  text-shadow: 2px 2px 4px #0009;
  margin-right: 30vw;
  width: 420px;
  /* margin-bottom: 10vh; */
}
.Carousel_slides_button {
  align-self: flex-start;
  height: 39px;
  width: auto;
  border-radius: 13px;
  border: none;
  margin-top: 30px;
  margin-right: 10%;
  padding: 10px 24px;
  background-color: #fd0000;
  color: white;
  font-size: 15px;
  font-weight: bold;
  font-family: "Questrial", Sans-serif;
  text-shadow: 2px 2px 4px #0009;
}
.Carousel_slides_button:focus {
  outline: none;
  box-shadow: none;
}
.Carousel_slides_button:hover {
  cursor: pointer;
}
.our_clients_text {
  text-align: center;
  color: #595959;
  font-family: "Questrial", Sans-serif;
  font-weight: 900;
  font-size: 2.75rem;
  margin-top: 90px;
}
.ver50 {
  margin-bottom: 60px;
}
.our_clients_content {
  display: flex;
  height: 300px;
  column-gap: 250px;
  margin-bottom: 80px;
  align-items: center;
}
.about_us_content {
  display: flex;
  align-items: center;
  width: 100%;
  margin-top: 60px;
}
.our_clients_container {
  background-color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.about_us_container {
  display: flex;
  align-items: center;
  flex-direction: column;
  height: 650px;
  background-color: #fff;
}
.pad50 {
  padding: 0 50px;
}

.pad200 {
  padding: 0 200px;
}
.about_us_images {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  width: 50%;
}
.about_us_text {
  text-align: center;
  color: #595959;
  font-family: "Questrial", Sans-serif;
  font-weight: 900;
  font-size: 1.75rem;
  margin-top: 30px;
  margin-bottom: 2vh;
}
.about_us_description {
  text-align: center;
  padding: 0 50px;
  color: #7a7a7a;
  font-family: "Questrial", Sans-serif;
  font-size: 1rem;
  line-height: 1.5;
}

.subHeading {
  height: 80%;
  background-position: center -50px;
  background-repeat: no-repeat;
  background-size: cover;
  color: white;
  text-align: center;
  text-shadow: 2px 2px 4px #0009;
  font-family: "Questrial", Sans-serif;
  font-size: 40px;
  font-weight: 100;
  padding-left: 5vw;
  padding-right: 5vw;
  display: flex;
  justify-content: center;
}
.subHeading::before {
  filter: brightness(50%);
}

.subHeading_text {
  margin-top: auto;
  margin-bottom: 10vh;
}

.navbar {
  text-decoration: none;
  color: #fff;
  padding: 0 20px;
}
.navbar:hover {
  text-decoration: none;
  color: #ccc;
  padding: 0 20px;
}
